import { GatsbyImage } from 'gatsby-plugin-image';
import React, { useEffect, useState } from 'react'

const Modal = (props) => {
  const [modalStatus, setModalStatus] = useState(false);
  useEffect(() => {
    if (props.modalData && props.modalData.gatsbyImageData && props.modalData.title) {
      setModalStatus(true);
    }
  }, [props.modalData]);

  // close modal
  const handleCloseModal = () => {
    setModalStatus(false);
  };

  return (
    <div className={`modal Modal ${modalStatus && 'is-active'}`}>
      <div
        className="modal-background"
        role={'none'}
        tabIndex="0" // eslint-disable-line
        onClick={ handleCloseModal }
      ></div>
      <div className="modal-content">
        <div className="card">
          <div className="card-image">
            <GatsbyImage image={props.modalData?.gatsbyImageData} alt={props.modalData.title} />
          </div>
          <div className="card-content">
            <div className="content has-text-centered">
              {props.modalData && props.modalData.title && <span>{props.modalData.title}</span>}
              {props.modalData && props.modalData.name && <h4>{props.modalData.name}</h4>}
              {props.modalData && props.modalData.bio && <p>{props.modalData.bio}</p>}
            </div>
          </div>
        </div>
      </div>
      <button className="modal-close is-large" aria-label="close" onClick={ handleCloseModal }></button>
    </div>
  )
}

export default Modal
